// db.js
export const openDatabase = () => {
    const dbName = 'wrDatabase';
    const dbVersion = 4;
  
    return new Promise((resolve, reject) => {
      const request = indexedDB.open(dbName, dbVersion);
  
      request.onupgradeneeded = (event) => {
        const db = event.target.result;

        // オブジェクトストアが存在すれば削除
        if (db.objectStoreNames.contains('Setting')) {
          db.deleteObjectStore('Setting');
          console.log('Setting object store deleted');
        } else {
          console.log('Setting object store not found');
        }
        const settingStore = db.createObjectStore('Setting', { keyPath: 'Id' });
        settingStore.createIndex('Name', 'Name', { unique: false });
        settingStore.createIndex('ShainBango', 'ShainBango', { unique: false });
        settingStore.createIndex('SagyoBasho', 'SagyoBasho', { unique: false });
        settingStore.createIndex('FromEmail', 'FromEmail', { unique: false });
        settingStore.createIndex('ToEmail', 'ToEmail', { unique: false });
        settingStore.createIndex('Subject', 'Subject', { unique: false });
  
        // 初期データを挿入
        const initialData = [
          { Id: 1, Name: '氏名'
            , ShainBango: '社員番号'
            , SagyoBasho: '作業場所'
            , FromEMail: 'from@example.com'
            , ToEMail: 'to@example.com'
            , Subject: 'subject' 
          },
        ];
        // データを追加
        initialData.forEach(item => {
          settingStore.add(item);
        });
      };


      request.onsuccess = (event) => {
        resolve(event.target.result);
      };
  
      request.onerror = (event) => {
        reject('Database error: ' + event.target.error);
      };
    });
  };
  