<template>
  <div>
    <h1>Setting</h1>
    <div v-if="settings && settings.length > 0">
      <ul>
        <li v-for="setting in settings" :key="setting?.Id">
          {{ setting?.Name }} (ID: {{ setting?.ShainBango }})
          {{ setting?.SagyoBasho }} 
          {{ setting?.FromEMail }} 
          {{ setting?.ToEMail }} 
          {{ setting?.Subject }} 
        </li>
      </ul>
    </div>
    <button @click="addSetting">Add Setting</button>
  </div>
</template>

<script>
import { addSetting, getSettingById, updateSetting, deleteSetting } from '../utils/crud';

export default {
  data() {
    return {
      settings: []
    };
  },
  created() {
    try {
      // API呼び出しやデータ取得
      this.fetchSettings(); // fetchUsers関数の結果を取得
    } catch (error) {
      console.error("ユーザー情報の取得に失敗しました:", error);
    }
  },
  methods: {
    async fetchSettings() {
      const setting1 = await getSettingById(1);
      this.settings = [setting1];
    },
    async addSetting() {
      const newSetting = { Id: 1, Name: 'Charlie' };
      await addSetting(newSetting);
      this.fetchSettings();
    },
    async updateSetting(setting) {
      setting.Name = setting.Name === 'Alice' ? 'Alice Updated' : 'Bob Updated';
      await updateSetting(setting);
      this.fetchSettings();
    },
    async deleteSetting(id) {
      await deleteSetting(id);
      this.fetchSettings();
    }
  }
};
</script>
