// crud.js
import { openDatabase } from './db';

export const addSetting = async (setting) => {
  const db = await openDatabase();
  const transaction = db.transaction(['Setting'], 'readwrite');
  const store = transaction.objectStore('Setting');

  const request = store.add(setting);

  request.onsuccess = () => {
    console.log('Setting added:', setting);
  };

  request.onerror = (event) => {
    console.error('Error adding setting:', event.target.error);
  };
};

export const getSettingById = async (Id) => {
  const db = await openDatabase();
  const transaction = db.transaction(['Setting'], 'readonly');
  const store = transaction.objectStore('Setting');

  const request = store.get(Id);

  return new Promise((resolve, reject) => {
    request.onsuccess = (event) => {
      resolve(event.target.result);
    };
    request.onerror = (event) => {
      reject('Error fetching setting:', event.target.error);
    };
  });
};

export const updateSetting = async (setting) => {
  const db = await openDatabase();
  const transaction = db.transaction(['Setting'], 'readwrite');
  const store = transaction.objectStore('Setting');

  const request = store.put(setting);

  request.onsuccess = () => {
    console.log('Setting updated:', setting);
  };

  request.onerror = (event) => {
    console.error('Error updating setting:', event.target.error);
  };
};

export const deleteSetting = async (Id) => {
  const db = await openDatabase();
  const transaction = db.transaction(['Setting'], 'readwrite');
  const store = transaction.objectStore('Setting');

  const request = store.delete(Id);

  request.onsuccess = () => {
    console.log('Setting deleted with ID:', Id);
  };

  request.onerror = (event) => {
    console.error('Error deleting setting:', event.target.error);
  };
};
